export class AppSettings {
	public static get BLOGPOSTS_EP(): String {
		return 'https://moigonz.com/blogposts';
	}
	public static get BLOGPOST_EP(): String {
		return `https://moigonz.com/blogpost`;
	}
	public static get OpenWeatherAPIKey(): String {
		return '79305d11f88bd1fb7ef3dd98faa5e911';
	}
	public static get CURRENT_WEATHER_BY_CITY_EP(): String {
		return 'https://api.openweathermap.org/data/2.5/weather?q=';
	}
	public static get CURRENT_WEATHER_BY_COORD_EP(): String {
		return 'https://api.openweathermap.org/data/2.5/weather?';
	}
}



